<template>
  <div class="content content-document">
    <div class="head-img">
      <img src="../common/images/head_img.jpeg" />
    </div>
    <h1>{{ document.name }}</h1>
    <h2><i class="fa fa-birthday-cake"></i>{{ document.birthday }}</h2>
    <h3>{{ document.email }}</h3>
    <p>{{ document.introduction }}</p>
  </div>
</template>
<script>
export default {
  data () {
    return {
      document: {
        age: 31,
        birthday: '1989-10-23',
        email: 'cj_zheng1023@hotmail.com',
        introduction: '有着将近九年的前后端开发经验，针对前后端主流的技术有着一定的了解。\n' +
        '能够熟练使用vue及相关技术栈。对小程序开发，electron，gulp，rollup，webpack，typescript等技术都有一定的了解。\n' +
        '工作细心，团队合作意识强，乐于分享和交流自己的心得。\n' +
        '喜欢探索和研究新技术，勇于实践。'
      }
    }
  }
}
</script>
<style scoped>
  .head-img{
    text-align: center;
  }
  .head-img img{
    width: 140px;
    height: 140px;
    border-radius: 50%;
  }
  h1{
    font-size: 36px;
    text-align: center;
    margin-top: 25px;
    margin-bottom: 30px;
  }
  h2, h3 {
    text-align: center;
    font-size: 14px;
  }
  h3{
    margin-bottom: 30px;
  }
  p{
    font-size: 16px;
  }
</style>



