<template>
  <div class="content content-skill">
    <ul>
      <li v-for="item in skills" :key="item.name" class="skill-item">
        <h5>{{ item.name }}</h5>
        <b-progress :value="item.score" show-value variant="success" striped animated class="mb-2"></b-progress>
      </li>
    </ul>
  </div>
</template>
<script>
import Vue from 'vue'
import bProgress from 'bootstrap-vue/es/components/progress/progress'
Vue.component('b-progress', bProgress)
export default {
  data () {
    return {
      skills: [{
        name: 'HTML5',
        score: 85
      }, {
        name: 'CSS3',
        score: 80
      }, {
        name: 'Vue',
        score: 80
      }, {
        name: 'jquery',
        score: 90
      }, {
        name: 'es6',
        score: 80
      }, {
        name: 'uni-app',
        score: 75
      }, {
        name: 'electron',
        score: 70
      }, {
        name: 'webpack',
        score: 70
      }, {
        name: 'gulp',
        score: 70
      }, {
        name: 'typescript',
        score: 65
      }]
    }
  }
}
</script>



