<template>
  <div class="content content-company">
    <ul>
      <li class="list-item" v-for="item in companyList" :key="item.name">
        <div class="item-icon">
          <i class="fa fa-graduation-cap"></i>
        </div>
        <div class="item-body">
          <h2 class="item-title">
            <span>{{ item.duration }}</span>
            <span>{{ item.name }}</span>
          </h2>
          <div class="item-content">
            <p>工作内容有：</p>
            <ul>
              <li v-for="(i, key) in item.descriptions" :key="i">
                <p>{{ key + 1 }}.{{ i }}</p>
              </li>
            </ul>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  data () {
    return {
      companyList: [{
        name: '东软集团股份有限公司',
        duration: '2012/07-至今',
        descriptions: ['制定前端开发规范，并应用到实际项目，提高前端团队开发效率。', '参与前端技术选型，调研，前端架构的设计以及对项目核心模块的开发工作。', '参与制定前后端接口规范。', '负责项目公共组件的开发，主要涉及基于jquery，vue等前端框架的公共组件开发。']
      }]
    }
  }
}
</script>



